body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#login-form > * {
    width: 250px;
}
.navbar-toggler-icon {
    width: 20px;
    height: 20px;
}
#home-page-container {
    height: 100vh;
}

#splash-container {
    height: 50vh;
    background-size: cover;
    background-repeat : no-repeat;
    background-position: center center;
}

.header-btn:hover {
    cursor: pointer;
    background: rgb(238, 238, 238);
}

#tab-container ul {
    width: 6rem;
}

.quantity-badge {
    position: absolute;
    top: -5px;
    right: -12px;
}

@media screen and (max-width: 500px){
    #tab-container {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100vw;
        z-index: 100;
        justify-content: space-evenly !important;
        flex-wrap: nowrap !important;
    }

    #tab-container ul {
        width: 4rem !important;
        padding-top: 0.5rem !important;
        padding-bottom: 0.5rem !important;
    }

    #tab-container p {
        font-size: 0.75rem;
    }
}





.tweet-portrait-container {
    width: 50px;
    height: 50px;
    min-width: 50px;
    min-height: 50px;
    overflow: hidden;
    border-radius: 50%;
    padding: 0;
    margin: 0;
    background-size: cover;
    background-position: center center;
}

.tweet-header > * {
    font-size: 0.8rem;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 0;
    min-width: 50px;
    text-align: left;
}

.tweet-body {
    font-size: 1rem;
    text-align: left;
    word-break:break-word;
}

#portrait-container {
    height: auto;
    width: 12rem;
    max-width: 40vw;
    overflow: hidden;
}

#portrait-container img {
    max-width: 100%;
    max-height: 100%;
}

@media screen and (max-width: 500px){
    #bio-text-container > * {
        font-size: 0.9rem !important;
    }
}


.user-card {
    height: 175px;
    width: 125px;
    background-position: center top;
    background-size: contain;
    background-repeat: no-repeat;
}

.user-card-portrait-container {
    height: 75px;
    width: 75px;
    border-radius: 50%;
    background-size:cover;
    background-position: center center;
    border: 3px rgb(223, 223, 223) solid;
}

.user-card-text-container {
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start
}


.request-btn {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    border: 2px white solid;
}

.request-btn-container {
    margin-top: -15%;
}

.request-btn .icon-sm {
    margin: 0 auto;
}



#messages-container > * {
    border-top: 1px rgb(223, 223, 223) solid;
}

#messages-container > *:hover {
    background: rgb(205, 237, 248) !important;
    cursor: pointer;
}

.trash-icon {
    color: silver;
}

.trash-icon:hover{
    color: blue;
}

@media screen and (max-width: 500px){
    #messages-container-header-bar{
    display: none !important;
    }

    .message-row{
        flex-wrap: wrap !important;
    }

    .message-from {
        order: 1 !important;
        width: 50%;
        font-weight: bold;
        font-size: 0.9rem;
    }

    .message-date {
        order: 2 !important;
        text-align: right !important;
        width: 50%;
    }

    .message-subject {
        order: 3 !important;
        width: 90%;
    }

    .trash-icon {
        order: 4 !important;
        text-align: right !important;
        margin-left: auto !important;
        width: 10%;
    }

}


#settings-content-container {
    width: 95%;
    max-width: 650px;
}

#settings-content-container p {
    margin-bottom: 0;
}

.btn-container > * {
    width: 9rem;
}

* {
  /* border: 1px red solid; */
  outline: none !important;
}

#root {
  min-height: 100vh;
  background: rgb(219, 244, 252);
}

.clickable:hover,
a:hover {
  cursor: pointer;
}

textarea {
  resize: none;
}

.bg-white {
  background: #FFF;
}

.min-h-100{
  min-height: 100vh;
}

.text-silver {
  color: silver;
  background: transparent;
}

.icon-sm {
  font-size: 1.2rem;
}

.icon-md{
  font-size: 1.7rem;
}

.icon-lg{
  font-size: 4rem;
}

.fa-icon-link:hover {
  color: blue;
  cursor:hover;
}

@media screen and (max-width: 400px) {
  .user-cards-container {
      justify-content: space-evenly !important;
      align-items: flex-start !important;
  }
}



#registration-content-container {
    width: 95%;
    max-width: 700px;
}
#login-content-container {
    width: 95%;
    max-width: 400px;
}
